.header__button {
  display: none;
}
@media (max-width: 767px) {
  .header__button {
    position: relative;
    display: flex;
    width: 24px;
    height: 12px;
    margin-left: auto;
    padding: 15px 0;
    background-color: rgba(255, 255, 255, 0);
    border: 0;
    transition: 0.6s cubic-bezier(0.55, 0, 0.1, 1);
    transform: translate(7px, 0);
    cursor: pointer;
  }
  .header__button::before {
    content: "";
    position: absolute;
    top: 5px;
    right: 7px;
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
    box-shadow: 0 6px 0 0 #f9fbfd, 0 12px 0 #f9fbfd;
  }
}

.is-open .header__button::after,
.is-open .header__button::before {
  content: "";
  position: absolute;
  top: 31px;
  right: 18px;
  width: 19px;
  height: 2px;
  background-color: #011c40;
  box-shadow: none;
}

.is-open .header__button::before {
  transform: rotate(-45deg);
}

.is-open .header__button::after {
  transform: rotate(45deg);
}

